import React from "react";
import { Link } from "react-router";

function Footer() {
	return (
		<div>
			{/* start footer section */}
			<footer className="site-footer">
				<div className="container">
					<div className="row">
						<div className="col-lg-3 col-12 mb-4">
							<img src="images\icons\shopper.png" className="logo img-fluid" alt="" />
						</div>

						<div className="col-lg-4 col-md-6 col-12 mb-4">
							<h5 className="site-footer-title mb-3">Quick Links</h5>

							<ul className="footer-menu">
								<li className="footer-menu-item">
									<Link to="/about" className="footer-menu-link">
										About
									</Link>
								</li>

								<li className="footer-menu-item">
									<Link to="/products" className="footer-menu-link">
										Products
									</Link>
								</li>

								<li className="footer-menu-item">
									<Link to="/order" className="footer-menu-link">
										Order
									</Link>
								</li>

								<li className="footer-menu-item">
									<Link to="/feedbacks" className="footer-menu-link">
										Feedbacks
									</Link>
								</li>

								<li className="footer-menu-item">
									<Link to="/contact" className="footer-menu-link">
										Contact
									</Link>
								</li>
							</ul>
						</div>

						<div className="col-lg-4 col-md-6 col-12 mx-auto">
							<h5 className="site-footer-title mb-3">Contact Infomation</h5>

							<p className="text-white d-flex mb-2">
								<i className="bi-telephone me-2"></i>

								<Link to="tel: 120-240-9600" className="site-footer-link">
									01200000000
								</Link>
							</p>

							<p className="text-white d-flex">
								<i className="bi-envelope me-2"></i>

								<Link
									to="mailto:info@yourgmail.com"
									className="site-footer-link">
									Deww@company.com
								</Link>
							</p>

							<p className="text-white d-flex mt-3">
								<i className="bi-geo-alt me-2"></i>
								Zagazig
							</p>

							<Link to="#" className="custom-btn btn mt-3">
								Get Direction
							</Link>
						</div>
					</div>
				</div>

				<div className="site-footer-bottom">
					<div className="container">
						<div className="row">
							<div className="col-lg-6 col-md-7 col-12">
								<p className="copyright-text mb-0">
									Copyright © 2036 <Link to="#">Kind Heart</Link> Charity Org.
									Design:{" "}
									<Link to="https://templatemo.com" target="_blank">
										TemplateMo
									</Link>
								</p>
							</div>

							<div className="col-lg-6 col-md-5 col-12 d-flex justify-content-center align-items-center mx-auto">
								<ul className="social-icon">
									<li className="social-icon-item">
										<Link to="#" className="social-icon-link bi-twitter"></Link>
									</li>

									<li className="social-icon-item">
										<Link to="#" className="social-icon-link bi-facebook"></Link>
									</li>

									<li className="social-icon-item">
										<Link to="#" className="social-icon-link bi-instagram"></Link>
									</li>

									<li className="social-icon-item">
										<Link to="#" className="social-icon-link bi-linkedin"></Link>
									</li>

									<li className="social-icon-item">
										<Link
											to="https://youtube.com/templatemo"
											className="social-icon-link bi-youtube"></Link>
									</li>
								</ul>
							</div>
						</div>
					</div>
				</div>
			</footer>
			{/* end footer section */}

		</div>
	);
}

export default Footer;
