import React from "react";
import { Link } from "react-router";

function GoShopping() {
	return (
        <div>
            {/* start Go Shopping section */}
			<section className="cta-section section-padding section-bg">
				<div className="container">
					<div className="row justify-content-center align-items-center">
						<div className="col-lg-5 col-12 ms-auto">
							<h2 className="mb-0">
								Make every day stylish <br /> Shop the best in clothing and
								accessories now
							</h2>
						</div>

						<div className="col-lg-5 col-12">
							<Link to="/products" className="custom-btn btn smoothscroll">
								Go Shopping
							</Link>
						</div>
					</div>
				</div>
            </section>
            {/* end Go Shopping section */}
            
		</div>
	);
}

export default GoShopping;
