import React, { useContext, useState } from "react";
import Navbar from "../Component/Navbar";
import SubNavbar from "../Component/SubNavbar";
import Footer from "../Component/Footer";
import { apiValue } from "../Data/AllData";
import { CartProvider, useCart } from "react-use-cart";
import { Link } from "react-router";

function Page() {
	const { addItem } = useCart();

	
	const products = useContext(apiValue);

	const [text, setText] = useState("");

	const getValue = (event) => {
		setText(event.target.value);
	};

	return (
		<CartProvider>
			{/* start search form */}

			<form
				className="custom-form search-form"
				action="#"
				method="post"
				role="form">
				<input
					className="form-control w-25 m-auto "
					onChange={getValue}
					type="search"
					placeholder="Search"
					aria-label="Search"></input>
			</form>

			{/* end search form  */}

			{/* start API Products */}

			<div className="container">
				<div className="row">
					{products.map((item) => {
						return text == "" ? (
							
							<Link to={`/info/${item.id}`} key={item.id} className="col-md-4 my-4 text-center ">
								<div className="custom-block-wrap">
									<img
										src={item.image}
										className="custom-block-image img-fluid"
										style={{
											height: "400px",
											width: "350px",
										}}
										alt=""
									/>

									<div className="custom-block">
										<div className="custom-block-body">
											<h4 className="mb-3">{item.title}</h4>

											<h5>{item.price}$</h5>
											<h6>{item.category}</h6>
										</div>
									</div>
										<button
										onClick={() => {
											addItem(item); 
											alert('Add To Cart');
										} 
											
										}
											className="custom-btn btn m-auto my-2 rounded">
											Add to cart
										</button>
								</div>
							</Link>
						) : item.id == text ? (
							<Link  to={`/info/${item.id}`}key={item.id} className="col-md-4 my-4 ">
								<div className="custom-block-wrap">
									<img
										src={item.image}
										className="custom-block-image img-fluid"
										style={{
											height: "400px",
											width: "350px",
										}}
										alt=""
									/>

									<div className="custom-block">
										<div className="custom-block-body">
											<h4 className="mb-3">{item.title}</h4>

											<h5>{item.price}$</h5>
											<h6>{item.category}</h6>
										</div>
										<button
											onClick={() => {
											addItem(item); 
											alert('Add To Cart');
										} }
											className="custom-btn btn m-auto my-2 rounded">
											Add to cart
										</button>
									</div>
								</div>
							</Link>
						) : item.title.indexOf(text.toUpperCase()) == 0 ? (
							<Link to={`/info/${item.id}`} key={item.id} className="col-md-4 my-4 ">
								<div className="custom-block-wrap">
									<img
										src={item.image}
										className="custom-block-image img-fluid"
										style={{
											height: "400px",
											width: "350px",
										}}
										alt=""
									/>

									<div className="custom-block">
										<div className="custom-block-body">
											<h4 className="mb-3">{item.title}</h4>

											<h5>{item.price}$</h5>
											<h6>{item.category}</h6>
										</div>
										<button
													onClick={() => {
											addItem(item); 
											alert('Add To Cart');
										} }
													
											className="custom-btn btn my-2 rounded">
											Add to cart
										</button>
									</div>
								</div>
							</Link>
						) : item.price.toString().indexOf(text) == 0 ? (
							<Link to={`/info/${item.id}`} key={item.id} className="col-md-4 my-4 ">
								<div className="custom-block-wrap">
									<img
										src={item.image}
										className="custom-block-image img-fluid"
										style={{
											height: "400px",
											width: "350px",
										}}
										alt=""
									/>

									<div className="custom-block">
										<div className="custom-block-body">
											<h4 className="mb-3">{item.title}</h4>

											<h5>{item.price}$</h5>
											<h6>{item.category}</h6>
										</div>
										<button
											onClick={() => {
											addItem(item); 
											alert('Add To Cart');
										} }
											className="custom-btn btn m-auto my-2 rounded">
											Add to cart
										</button>
									</div>
								</div>
							</Link>
						) : null;
					})}
				</div>
			</div>
			{/* end API Products */}
		</CartProvider>
	);
}

function AllProducts() {
	return (
		<CartProvider>
			<SubNavbar />
			<Navbar />
			<Page />
			<Footer />
		</CartProvider>
	);
}
export default AllProducts;
