import React from "react";
import { Link, NavLink } from "react-router-dom";
import { useCart } from "react-use-cart";
function Navbar() {
	const { totalItems } = useCart();
	return (
		// start Navbar secion
		<nav className="navbar navbar-expand-lg bg-light shadow-lg">
			<div className="container">
				<Link className="navbar-brand" to="/">
					<img
						src="images/icons/shopper.png"
						className="logo img-fluid"
						alt=""
					/>
					<span>
						Deww
						<small>E-commerce Store</small>
					</span>
				</Link>

				<button
					className="navbar-toggler"
					type="button"
					data-bs-toggle="collapse"
					data-bs-target="#navbarNav"
					aria-controls="navbarNav"
					aria-expanded="false"
					aria-label="Toggle navigation">
					<span className="navbar-toggler-icon"></span>
				</button>

				<div className="collapse navbar-collapse" id="navbarNav">
					<ul className="navbar-nav ms-auto">
						<li className="nav-item">
							<NavLink className="nav-link click-scroll" to="/">
								Home
							</NavLink>
						</li>

						<li className="nav-item">
							<NavLink className="nav-link click-scroll" to="/about">
								About
							</NavLink>
						</li>

						<li className="nav-item">
							<NavLink className="nav-link click-scroll" to="/products">
								Products
							</NavLink>
						</li>

						<li className="nav-item">
							<NavLink className="nav-link click-scroll" to="/order">
								Order
							</NavLink>
						</li>

						<li className="nav-item">
							<NavLink
								className="nav-link click-scroll"
								to="/feedbacks"
								>
								Feedbacks
							</NavLink>

						
						</li>

						<li className="nav-item">
							<NavLink className="nav-link click-scroll" to="/contact">
								Contact 
								
							</NavLink>
						</li>

						<li className="nav-item ms-3">
							<NavLink
								className="nav-link custom-btn custom-border-btn btn"
								to="/cart">
								
								Cart
								<span
									className="position-absolute top-0
                    start-100 translate-middle badge rounded-pill"
									style={{
										width: "29px",
										height: "20px",
										backgroundColor: "#5bc0ab",
									}}>
									{totalItems}
								</span>
							</NavLink>
						</li>
					</ul>
				</div>
			</div>
		</nav>
		// end Navbar Section
	);
}

export default Navbar;
