import { createRoot } from "react-dom/client";
import {createBrowserRouter,RouterProvider} from "react-router-dom";
import App from "./App";
import { AllData } from "./Data/AllData";
import ScrollToTop from "react-scroll-to-top";
import { CartProvider } from "react-use-cart";
import AllAbout from "./About/AllAbout";
import AllProducts from "./Products/AllProducts";
import AllCart from "./Cart/AllCart";
import AllOrder from "./Order/AllOrder";
import AllContact from "./Contact/AllContact";
import AllFeedbacks from "./Feedbacks/AllFeedbacks";
import AllInfo from "./Info/AllInfo";

const router = createBrowserRouter([
  {
    path: "/",
    element:
      <App/>
    ,
  },
  {
    path: "about",
    element: <AllAbout/>,
  },
  {
    path: "products",
    element: <AllProducts/>,
  },
  {
    path: "cart",
    element: <AllCart/>,
  },
  {
    path: "order",
    element: <AllOrder/>,
  },
  {
    path: "contact",
    element: <AllContact/>,
  },
  {
    path: "feedbacks",
    element: <AllFeedbacks/>,
  },
  {
    path: "info/:id",
    element: <AllInfo/>,
  },
  
]);

createRoot(document.getElementById("root")).render(
  <AllData>
    <CartProvider>
    <RouterProvider router={router} />
    <ScrollToTop
      smooth
      />
      </CartProvider>
  </AllData>
);
