import React from "react";
import SubNavbar from "../Component/SubNavbar";
import Navbar from "../Component/Navbar";
import Footer from "../Component/Footer";
import { CartProvider } from "react-use-cart";
import { useParams } from "react-router-dom";
import { useCart } from "react-use-cart";
import { useContext } from "react";
import { apiValue } from "../Data/AllData";

function AllInfo() {
  	const { addItem } = useCart();

	
	const products = useContext(apiValue);

	const { id } = useParams();
	const item = products.find((p) => p.id === parseInt(id));

	if (!item) return <h2>Product not found</h2>;
	return (
		<CartProvider>
			<SubNavbar />
			<Navbar />
			<div className="col-md-5 m-auto my-4 text-center">
				<div className="custom-block-wrap">
					<img
						src={item.image}
						className="custom-block-image img-fluid m-auto"
						style={{
							height: "400px",
							width: "350px",
						}}
						alt=""
					/>

					<div className="custom-block">
						<div className="custom-block-body">
							<h4 className="mb-3">{item.title}</h4>

							<h5>{item.price}$</h5>
							<h6>{item.category}</h6>
							<h6>{item.description}</h6>
							<h6>Rate : {item.rating.rate}</h6>
						</div>
					</div>
					<button
						onClick={() => {
							addItem(item);
							alert("Add To Cart");
						}}
						className="custom-btn btn m-auto my-2 rounded">
						Add to cart
					</button>
				</div>
			</div>
			<Footer />
		</CartProvider>
	);
}

export default AllInfo;
