import React from "react";
import { Link } from "react-router";

function Welcome() {
	return (
		<div>
			{/* start Welcome message section */}
			<section className="section-padding">
				<div className="container">
					<div className="row">
						<div className="col-lg-10 col-12 text-center mx-auto">
							<h2 className="mb-5">Welcome to Our Store</h2>
						</div>

						<div className="col-lg-3 col-md-6 col-12 mb-4 mb-lg-0">
							<div className="featured-block d-flex justify-content-center align-items-center">
								<Link to="/products" className="d-block">
									<img
										src="images\icons\brand-engagement.png"
										className="featured-block-image img-fluid"
										alt=""
									/>

									<p className="featured-block-text">
										Prepared for you with <strong>All Love</strong>
									</p>
								</Link>
							</div>
						</div>

						<div className="col-lg-3 col-md-6 col-12 mb-4 mb-lg-0 mb-md-4">
							<div className="featured-block d-flex justify-content-center align-items-center">
								<Link to="/products" className="d-block">
									<img
										src="images\icons\shopping-bag (1).png"
										className="featured-block-image img-fluid"
										alt=""
									/>

									<p className="featured-block-text">
										Ease of <strong>Shopping</strong>
									</p>
								</Link>
							</div>
						</div>

						<div className="col-lg-3 col-md-6 col-12 mb-4 mb-lg-0 mb-md-4">
							<div className="featured-block d-flex justify-content-center align-items-center">
								<Link to="/products" className="d-block">
									<img
										src="images\icons\shopping-bag.png"
										className="featured-block-image img-fluid"
										alt=""
									/>

									<p className="featured-block-text">
										<strong>Offers</strong> and <strong>Discounts</strong>
									</p>
								</Link>
							</div>
						</div>

						<div className="col-lg-3 col-md-6 col-12 mb-4 mb-lg-0">
							<div className="featured-block d-flex justify-content-center align-items-center">
								<Link to="/products" className="d-block">
									<img
										src="images\icons\shopping-couple.png"
										className="featured-block-image img-fluid"
										alt=""
									/>

									<p className="featured-block-text">
										The most beautiful <strong>Gifts</strong> 
									</p>
								</Link>
							</div>
						</div>
					</div>
				</div>
			</section>
			{/* end Welcome message section */}

		</div>
	);
}

export default Welcome;
