import React from "react";
import { CartProvider } from "react-use-cart";
import SubNavbar from "../Component/SubNavbar";
import Navbar from "../Component/Navbar";
import { useCart } from "react-use-cart";
import { Link } from "react-router";

function Cart() {
	const {
		isEmpty,
		totalUniqueItems,
		items,
		updateItemQuantity,
		removeItem,
		cartTotal,
		totalItems,
		emptyCart,
	} = useCart();

	if (isEmpty)
		return (
			<div className="text-center">
				<h3 className="text-center m-5">Your cart is empty</h3>
				<Link to="/products">
					<button className="custom-btn btn m-auto my-2 rounded">
						Shop Now
					</button>
				</Link>
			</div>
		);

	return (
		<>
			<div className="text-center m-4">
				<h1>Cart  ({totalUniqueItems})</h1>
				<button className="btn btn-danger m-3" onClick={() => emptyCart()}>
					Empty Cart
				</button>
			</div>
			<ul>
				<div className="container">
					<div className="row">
						<div className="col-md-10 m-auto">
							<table className="table table-light text-center">
								<thead>
									<tr>
										<th>ID</th>
										<th>Image</th>
										<th>Name</th>
										<th>Quantity</th>
										<th>Price</th>
										<th>Operation</th>
									</tr>
								</thead>

								<tbody>
									{items.map((item) => (
										<tr>
											<td>{item.id}</td>
											<td>
												<img
													src={item.image}
													style={{ height: "100px" }}
													alt=""
												/>
											</td>
											<td>{item.title}</td>
											<td>{item.quantity}</td>
											<td>{Math.ceil(item.price * item.quantity)}$</td>
											<td>
												<button
													className="btn btn-warning m-2"
													onClick={() =>
														updateItemQuantity(
															item.id,
															(item.quantity ?? 0) - 1
														)
													}>
													-
												</button>
												<button
													className="btn btn-success m-2"
													onClick={() =>
														updateItemQuantity(
															item.id,
															(item.quantity ?? 0) + 1
														)
													}>
													+
												</button>
												<button
													className="btn btn-danger m-2"
													onClick={() => removeItem(item.id)}>
													&times;
												</button>
											</td>
										</tr>
									))}
								</tbody>
								<tfoot>
									<tr>
										<td></td>
										<td></td>
										<td>Total</td>
										<td>{totalItems}</td>
										<td>{Math.ceil(cartTotal)}$</td>
										<td>
											<Link
												to="/products"
												className="btn"
												style={{
													backgroundColor: "#5bc0ab",
												}}>
												Continue Shopping
											</Link>
										</td>
									</tr>
								</tfoot>
							</table>
						</div>
					</div>
				</div>
			</ul>
		</>
	);
}

function AllCart() {
	return (
		<CartProvider>
			<SubNavbar />
			<Navbar />
			<Cart />
		</CartProvider>
	);
}

export default AllCart;
