import React from "react";

function HappyCust() {
	return (
		<div>

			{/* start New Collection section */}
			<section class="news-detail-header-section text-center">
				<div class="section-overlay"></div>

				<div class="container">
					<div class="row">
						<div class="col-lg-12 col-12">
							<h1 class="text-white">New Collection</h1>
						</div>
					</div>
				</div>
			</section>
			{/* end New Collection section */}

			{/* start happy customers section */}
			<section class="testimonial-section section-padding section-bg">
				<div class="container">
					<div class="row">
						<div class="col-lg-8 col-12 mx-auto">
							<h2 class="mb-lg-3">Happy Customers</h2>

							<div
								id="testimonial-carousel"
								class="carousel carousel-fade slide"
								data-bs-ride="carousel">
								<div class="carousel-inner">
									<div class="carousel-item active">
										<div class="carousel-caption">
											<h4 class="carousel-title">
												Honestly, the website is amazing! The shopping
												experience was easy and fast.
											</h4>

											<small class="carousel-name">
												<span class="carousel-name-title">Maria</span>, Boss
											</small>
										</div>
									</div>

									<div class="carousel-item">
										<div class="carousel-caption">
											<h4 class="carousel-title">
												I received my order in no time and the quality was
												excellent — definitely not my last time ordering from
												you!
											</h4>

											<small class="carousel-name">
												<span class="carousel-name-title">Thomas</span>, Partner
											</small>
										</div>
									</div>

									<div class="carousel-item">
										<div class="carousel-caption">
											<h4 class="carousel-title">
												Thank you for the attention to detail. The packaging was
												so elegant and the service was excellent.
											</h4>

											<small class="carousel-name">
												<span class="carousel-name-title">Jane</span>, Advisor
											</small>
										</div>
									</div>

									<div class="carousel-item">
										<div class="carousel-caption">
											<h4 class="carousel-title">
												What I loved the most is the variety of products and the
												reasonable prices. I could really feel the difference.
											</h4>

											<small class="carousel-name">
												<span class="carousel-name-title">Bob</span>,
												Entreprenuer
											</small>
										</div>
									</div>

									<ol class="carousel-indicators">
										<li
											data-bs-target="#testimonial-carousel"
											data-bs-slide-to="0"
											class="active">
											<img
												src="images/avatar/portrait-beautiful-young-woman-standing-grey-wall.jpg"
												class="img-fluid rounded-circle avatar-image"
												alt="avatar"
											/>
										</li>

										<li
											data-bs-target="#testimonial-carousel"
											data-bs-slide-to="1"
											class="">
											<img
												src="images/avatar/portrait-young-redhead-bearded-male.jpg"
												class="img-fluid rounded-circle avatar-image"
												alt="avatar"
											/>
										</li>

										<li
											data-bs-target="#testimonial-carousel"
											data-bs-slide-to="2"
											class="">
											<img
												src="images/avatar/pretty-blonde-woman-wearing-white-t-shirt.jpg"
												class="img-fluid rounded-circle avatar-image"
												alt="avatar"
											/>
										</li>

										<li
											data-bs-target="#testimonial-carousel"
											data-bs-slide-to="3"
											class="">
											<img
												src="images/avatar/studio-portrait-emotional-happy-funny.jpg"
												class="img-fluid rounded-circle avatar-image"
												alt="avatar"
											/>
										</li>
									</ol>
								</div>
							</div>
						</div>
					</div>
				</div>
			</section>
			{/* end happy customers section */}

		</div>
	);
}

export default HappyCust;
