import React from "react";
import SubNavbar from "../Component/SubNavbar";
import Navbar from "../Component/Navbar";
import { CartProvider } from "react-use-cart";
import Footer from "../Component/Footer";

function AllOrder() {
	return (
		<CartProvider>
			<SubNavbar />
			<Navbar />
			<div>
				{/* start order section */}
				<section className="volunteer-section section-padding" id="section_4">
					<div className="container">
						<div className="row">
							<div className="col-lg-6 col-12">
								<h2 className="text-white mb-4">Make an Order</h2>

								<form
									className="custom-form client-form mb-5 mb-lg-0"
									action="#"
									method="post"
									role="form">
									<h3 className="mb-4">Become a client today</h3>

									<div className="row">
										<div className="col-lg-6 col-12">
											<input
												type="text"
												name="client-name"
												id="client-name"
												className="form-control"
												placeholder="Enter your name"
												required
											/>
										</div>

										<div className="col-lg-6 col-12">
											<input
												type="email"
												name="client-email"
												id="client-email"
												pattern="[^ @]*@[^ @]*"
												className="form-control"
												placeholder="Enter your Email"
												required
											/>
										</div>

										<div className="col-lg-6 col-12">
											<input
												type="text"
												name="client-address"
												id="client-address"
												className="form-control"
												placeholder="Enter your address"
												required
											/>
										</div>

										<div className="col-lg-6 col-12">
											<div className="input-group input-group-file">
												<input
													type="file"
													className="form-control"
													id="inputGroupFile02"
												/>

												<label className="input-group-text" for="inputGroupFile02">
													Upload your Order
												</label>

												<i className="bi-cloud-arrow-up ms-auto"></i>
											</div>
										</div>
									</div>

									<textarea
										name="client-message"
										rows="3"
										className="form-control"
										id="client-message"
										placeholder="Comment (Optional)"></textarea>

									<button type="submit" className="form-control">
										Submit
									</button>
								</form>
							</div>

							<div className="col-lg-6 col-12">
								<img
									src="images/smiling-casual-woman-dressed-volunteer-t-shirt-with-badge.jpg"
									className="client-image img-fluid"
									alt=""
								/>

								<div className="custom-block-body text-center">
									<h4 className="text-white mt-lg-3 mb-lg-3">About Client</h4>

									<p className="text-white">
										Make sure to enter your data carefully so that the order is
										delivered successfully.
									</p>
								</div>
							</div>
						</div>
					</div>
				</section>
				{/* end order section */}

			</div>
			<Footer />
		</CartProvider>
	);
}

export default AllOrder;
